@charset "UTF-8";
.glass-case * {
  -webkit-box-sizing: border-box !important;
  -moz-box-sizing: border-box !important;
  box-sizing: border-box !important;
  padding: 0;
  margin: 0;
  border: 0
}

.glass-case :after, .glass-case :before {
  -webkit-box-sizing: border-box !important;
  -moz-box-sizing: border-box !important;
  box-sizing: border-box !important
}

.glass-case {
  position: relative
}

.gc-display-area {
  position: absolute;
  //overflow: hidden;
  //border: 2px solid #7f7f7f;
  padding: 2px;
  background-color: #fff
}

.gc-display-container {
  position: relative;
  overflow: hidden;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%
}

.gc-display-display {
  position: relative;
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
  cursor: crosshair;
  opacity: 1
}

.gc-lens {
  position: absolute;
  overflow: hidden;
  z-index: 1000;
  cursor: crosshair;
  background: #ffffff;
  opacity: .5 !important;
  -ms-filter: "alpha(Opacity=50)";
  filter: alpha(opacity=50) !important;
  -moz-opacity: .5;
  -khtml-opacity: .5
}

.gc-zoom {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: auto;
  z-index: 1040 !important;
  border: 2px solid #7f7f7f;
  padding: 2px;
  background-color: #fff
}

.gc-zoom-inner {
  cursor: crosshair;
  border: 0 !important;
  left: -2px !important;
  top: -2px !important;
  margin: 0 !important;
  z-index: 1010 !important
}

.gc-zoom-container {
  position: relative;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%
}

.gc-zoom-container img {
  position: relative;
  max-width: none !important
}

.gc-caption-container {
  position: absolute;
  overflow: hidden;
  width: 100%;
  padding: 2px;
  z-index: 1050 !important;
  background-color: rgba(255, 255, 255, .5);
  line-height: 1.6;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-style: italic;
  color: #333;
  font-size: .9em
}

.gc-caption-container div {
  position: relative;
  padding: 0 12px
}

.gc-caption-intop {
  left: 0;
  top: 0
}

.gc-caption-inbottom {
  left: 0;
  bottom: 0
}

.gc-caption-outbottom, .gc-caption-outtop {
  width: 100%;
  width: calc(100% + 4px);
  border: 2px solid #7f7f7f;
  background: #fff
}

.gc-caption-outtop {
  top: 0;
  left: -2px;
  -webkit-transform: translate(0, -100%);
  -moz-transform: translate(0, -100%);
  -ms-transform: translate(0, -100%);
  -o-transform: translate(0, -100%);
  transform: translate(0, -100%)
}

.gc-caption-outbottom {
  bottom: 0;
  left: -2px;
  -webkit-transform: translate(0, 100%);
  -moz-transform: translate(0, 100%);
  -ms-transform: translate(0, 100%);
  -o-transform: translate(0, 100%);
  transform: translate(0, 100%)
}

.gc-alignment-center {
  text-align: center
}

.gc-alignment-left {
  text-align: left
}

.gc-alignment-right {
  text-align: right
}

.gc-thumbs-area {
  position: absolute;
  overflow: hidden;
  background-color: transparent
}

.gc-thumbs-area-next, .gc-thumbs-area-prev {
  position: absolute;
  z-index: 1010
}

.gc-thumbs-area.gc-hz .gc-thumbs-area-next, .gc-thumbs-area.gc-hz .gc-thumbs-area-prev {
  top: 0;
  height: 100%;
  min-height: 100%;
  width: 5%
}

.gc-thumbs-area.gc-vt .gc-thumbs-area-next, .gc-thumbs-area.gc-vt .gc-thumbs-area-prev {
  left: 0;
  width: 100%;
  min-width: 100%;
  height: 5%
}

.gc-thumbs-area.gc-hz .gc-thumbs-area-prev {
  left: 0
}

.gc-thumbs-area.gc-hz .gc-thumbs-area-next {
  right: 0
}

.gc-thumbs-area.gc-vt .gc-thumbs-area-prev {
  top: 0
}

.gc-thumbs-area.gc-vt .gc-thumbs-area-next {
  bottom: 0
}

.gc-thumbs-area-next:hover, .gc-thumbs-area-prev:hover {
  opacity: 1;
  cursor: pointer;
  cursor: hand
}

.gc-thumbs-area-next .gc-disabled, .gc-thumbs-area-next .gc-disabled:hover, .gc-thumbs-area-prev .gc-disabled, .gc-thumbs-area-prev .gc-disabled:hover {
  opacity: .3;
  -ms-filter: "alpha(Opacity=30)";
  filter: alpha(opacity=30);
  -moz-opacity: .3;
  -khtml-opacity: .3;
  cursor: default
}

.glass-case ul {
  position: relative;
  top: 0;
  left: 0;
  margin: 0;
  border: 0;
  padding: 0;
  height: 100%;
  list-style: none
}

.glass-case ul li {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  //border: 2px solid #7f7f7f;
  padding: 2px;
  background-color: #fff;
  cursor: pointer;
}

.gc-thumbs-area.gc-hz li {
  display: inline;
  float: left;
  max-width: 100%;
  max-height: 100%;
  margin-right: 5px
}

.gc-thumbs-area.gc-vt li {
  margin-bottom: 5px
}

.glass-case ul li .gc-li-display-container {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
  display: inline-block
}

.glass-case ul li:hover {
  border-color: #4f4f4f;
  cursor: pointer;
  cursor: hand
}

.glass-case ul li.gc-active, .glass-case ul li.gc-active:hover {
  border-color: $dark-grey;
  cursor: default
}

.glass-case ul li .gc-li-display-container img {
  position: relative;
  top: 50%;
  left: 50%
}

.gc-overlay-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2147483647 !important;
  background-color: #ccc;
  background-color: rgba(51, 51, 51, .95)
}

.gc-overlay-gcontainer {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0
}

.gc-overlay-container {
  position: relative;
  overflow: auto;
  width: 100%;
  height: 100%;
  padding: 0
}

.gc-overlay-container-display {
  position: absolute;
  overflow: auto;
  width: 100%;
  height: 100%;
  padding: 0
}

.gc-ocd-top {
  top: 0
}

.gc-ocd-bottom {
  bottom: 0
}

.gc-overlay-fit {
  padding: 4em 5.5em 1em;
  overflow: hidden
}

.gc-overlay-display {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  cursor: crosshair
}

.gc-overlay-display-center {
  max-width: 100%;
  max-height: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%)
}

.gc-overlay-display-vcenter {
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%)
}

.gc-overlay-display-hcenter {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%)
}

.gc-overlay-top-icons {
  position: absolute;
  top: 0;
  padding: 1em 1.5em 1em 0;
  width: 100%;
  height: 4em
}

.gc-overlay-left-icons {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 1em 0 1.5em;
  height: 100%;
  width: 5.5em
}

.gc-overlay-right-icons {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1.5em 0 1em;
  height: 100%;
  width: 5.5em
}

@font-face {
  font-family: linea-arrows-10;
  src: url(../fonts/linea-arrows-10.eot);
  src: url(../fonts/linea-arrows-10.eot?#iefix) format("embedded-opentype"), url(../fonts/linea-arrows-10.woff) format("woff"), url(../fonts/linea-arrows-10.ttf) format("truetype"), url(../fonts/linea-arrows-10.svg#linea-arrows-10) format("svg");
  font-weight: 400;
  font-style: normal
}

.gc-icon:before {
  line-height: 1
}

.gc-icon {
  font-family: linea-arrows-10 !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  z-index: 1020;
  cursor: pointer;
  cursor: hand;
  color: #fff;
  opacity: .7;
  -ms-filter: "alpha(Opacity=70)";
  filter: alpha(opacity=70);
  -moz-opacity: .7;
  -khtml-opacity: .7;
  height: 1em;
  width: 1em;
  line-height: 0;
  background: #ccc;
  background: rgba(204, 204, 204, .5);
  border: 2px solid #7f7f7f
}

.gc-icon:hover {
  opacity: 1;
  -ms-filter: "alpha(Opacity=100)";
  filter: alpha(opacity=100);
  -moz-opacity: 1;
  -khtml-opacity: 1
}

.gc-disabled .gc-icon, .gc-disabled .gc-icon:hover {
  opacity: .3;
  -ms-filter: "alpha(Opacity=30)";
  filter: alpha(opacity=30);
  -moz-opacity: .3;
  -khtml-opacity: .3;
  cursor: default
}

.gc-icon-next {
  top: 50%;
  right: -2px
}

.gc-icon-next:before {
  position: absolute;
  top: -2px;
  right: -2px;
  content: "\e04b"
}

.gc-icon-next-vt {
  bottom: 0;
  left: 50%
}

.gc-icon-next-vt:before {
  position: absolute;
  top: -2px;
  left: -2px;
  content: "\e019"
}

.gc-display-area .gc-icon-next {
  font-size: 2em !important
}

.gc-thumbs-area-next .gc-icon-next {
  right: 0;
  font-size: 2em !important
}

.gc-thumbs-area-next .gc-icon-next-vt {
  font-size: 1.5em !important
}

.gc-overlay-area .gc-icon-next {
  position: relative;
  right: 0;
  font-size: 3em
}

.gc-icon-prev {
  top: 50%;
  left: -2px
}

.gc-icon-prev:before {
  position: absolute;
  top: -2px;
  left: -2px;
  content: "\e03f"
}

.gc-icon-prev-vt {
  top: 0;
  left: 50%
}

.gc-icon-prev-vt:before {
  position: absolute;
  top: -2px;
  left: -2px;
  content: "\e083"
}

.gc-display-area .gc-icon-prev, .gc-thumbs-area-prev .gc-icon-prev {
  font-size: 2em !important
}

.gc-thumbs-area-prev .gc-icon-prev-vt {
  font-size: 1.5em !important
}

.gc-overlay-area .gc-icon-prev {
  position: relative;
  left: 0;
  font-size: 3em
}

.gc-icon-download {
  right: -2px;
  bottom: 0;
  font-size: 2em;
  overflow: hidden
}

.gc-icon-download:before {
  position: absolute;
  top: -4px;
  left: -2px;
  content: "\e064"
}

.gc-icon-close {
  position: relative;
  font-size: 2em;
  float: right
}

.gc-icon-close:before {
  position: absolute;
  content: "\e04a";
  top: -2px;
  right: -2px
}

.gc-icon-enlarge {
  position: relative;
  float: right;
  margin-right: .25em;
  font-size: 2em
}

.gc-icon-enlarge:before {
  position: absolute;
  content: '\e017';
  top: -2px;
  right: -2px
}

.gc-icon-compress {
  position: relative;
  float: right;
  margin-right: .25em;
  font-size: 2em
}

.gc-icon-compress:before {
  position: absolute;
  content: '\e053';
  top: -2px;
  right: -2px
}

.gc-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 31px;
  height: 31px;
  margin-left: -15px;
  margin-top: -15px;
  background-image: url(../images/loader.gif)
}

.gc-loading3 {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -15px;
  font-size: .25em;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(102, 153, 102, .2);
  border-right: 1.1em solid rgba(102, 153, 102, .2);
  border-bottom: 1.1em solid rgba(102, 153, 102, .2);
  border-left: 1.1em solid #696;
  -webkit-animation: loadGC 1.1s infinite linear;
  animation: loadGC 1.1s infinite linear
}

.gc-loading3, .gc-loading3:after {
  border-radius: 50%;
  width: 30px;
  height: 30px
}

@-webkit-keyframes loadGC {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
  }
}

@keyframes loadGC {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg)
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg)
  }
}

.gc-hide {
  display: none;
  z-index: 0;
  opacity: 0;
  -ms-filter: "alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0
}

.gc-noscroll {
  overflow: hidden
}

.gc-start {
  display: none;
  z-index: 0;
  opacity: 0;
  -ms-filter: "alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0
}

.gc-icon.gc-icon-prev, .gc-icon.gc-icon-next, .gc-icon.gc-icon-next-vt, .gc-icon.gc-icon-prev-vt {
  color: $grey !important;
  background: none !important;
  border: 1px solid $grey;
}

.gc-icon.gc-icon-download {
  display: none !important;
}